<template>
    <div class="classif">
      <div class="card" v-for="(i,index) in data">
        <img :src="JSON.parse(i.images)[0]" />
        <p>{{i.name}}</p>
        <p>{{i.price}}</p>
      </div>
    </div>
  </template>
  
  <script>
    export default {
      computed:{
      },
      data(){
        return{
          data:[],
        }
      },
      methods:{
        getdata(){
          let type=this.$route.params.name;
          this.$api.gettype({type:type}).then(res=>{
            // console.log(res.data.data);
            this.data=res.data.data
            // console.log(this.data);
          })
        }
      },
      mounted(){
        console.log(this.$route.params.name);
        this.getdata()
      },
      watch:{
        '$route'(to,from){
          if(to.params.name != from.params.name){
            this.getdata()
            console.log(to.params.name+'xxxx'+from.params.name);
          }
        }
      }
    }
  </script>
  <style lang="less" scoped>
      .classif{
        display: flex;
        flex-wrap: wrap;
        padding: 10px;
      }
     .card{
      --n:4;
      --gap: calc((100% - 200px * var(--n)) / var(--n) /2);
      width: 200px;
      height: 300px;
      border-radius: 10px;
      background: rgb(255, 255, 255);
      margin: 10px var(--gap);
     }
     img{
      border-radius: 10px 10px 0 0;width: 200px;object-fit: cover;height: 260px;
     }
  </style>
  